import React, { FC } from "react";
import { ChatMsgDetail } from "../../store/ChatAction";
import { isAgent } from "../../utils";
import { ChatMessageBubble } from "../ChatMessageBubble";

export interface ChatTextMessageProps {
    readonly message: ChatMsgDetail;
    readonly sibling: boolean;
}

export const ChatTextMessage: FC<ChatTextMessageProps> = props => {
    const agentMessage = isAgent(props.message.nick);

    return (
        <div className="chat-msg">
            <div className="chat-text-container">
                <ChatMessageBubble owner={agentMessage ? "others" : "mine"} hasTail={agentMessage && !props.sibling}>
                    <span>{props.message.msg.text}</span>
                </ChatMessageBubble>
            </div>
        </div>
    );
};

