import { ServerConfig } from "@xapp/stentor-chat-widget";
import { createContext, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { ChatState } from "../store/ChatState";
import { throwBadKind } from "../utils/throwBadKind";
import { ChatServer } from "./ChatServer";
import { LogChat } from "./LogChat";
import { StentorDirectChat } from "./StentorDirectChat";
import { StentorLocalChat } from "./StentorLocalChat";
import { StentorServerChat } from "./StentorServerChat";

function createChatServerCore(config: ServerConfig, token?: string): ChatServer {
    switch (config.type) {
        case "direct":
            return new StentorDirectChat({
                url: config.serverUrl,
                key: config.accountKey
            });
        case "websocket":
            return new StentorServerChat({
                url: config.serverUrl,
                token
            });
        case "local":
            return new StentorLocalChat();
        default:
            throw throwBadKind(config.type);
    }
}

export function createChatServer(config: ServerConfig, token?: string): ChatServer {
    return new LogChat(createChatServerCore(config, token));
}

export const ChatServerContext = createContext<ChatServer>(null);

export function useChatServer(config: ServerConfig): ChatServer {

    const [server, setServer] = useState<ChatServer>();

    const token = useSelector<ChatState, string | null>(state => state.connection.token);

    const dispatch = useDispatch();

    useEffect(() => {
        const newServer = createChatServer(config, token);

        newServer.init(dispatch);

        setServer(newServer);
        return () => {
            newServer.dispose();
        };
    }, [token, config, dispatch]);

    return server;
}
