import React, { ChangeEvent, DragEvent, FC, FormEvent, useState } from "react";
import { SendButton } from "../SendButton";
import "./Input.scss";

export interface Props {
    onSubmit: (msg: string) => void,
    onFileUpload: (event: DragEvent<HTMLInputElement>) => void,
    addClass: string,
    onChange: () => void,
    // onFocus: () => void,
}

export interface State {
    dragover: boolean;
}

const Input: FC<React.PropsWithChildren<Props>> = props => {
    const [rawInput, setRawInput] = useState("");
    const [dragover, setDragover] = useState(false);

    function onDragOver(event: React.DragEvent<HTMLInputElement>) {
        setDragover(true);
        event.preventDefault();

        event.dataTransfer.dropEffect = "copy";
    }

    function onDrop(event: React.DragEvent<HTMLInputElement>) {
        setDragover(false);

        props.onFileUpload(event);
    }

    function onDragLeave() {
        setDragover(false);
    }

    function handleOnSubmit(event?: FormEvent) {
        event && event.preventDefault();
        props.onSubmit(rawInput); // send to the widget
        setRawInput("");
    }

    function handleOnChange(event: ChangeEvent<HTMLInputElement>) {
        setRawInput(event.target.value);
        props.onChange();
    }

    const className = [
        "input-container",
        props.addClass,
        dragover ? "drag-drop-zone" : ""
    ].join(" ");

    return (
        <div className={className}
            onDrop={onDrop}
            onDragOver={onDragOver}
            onDragLeave={onDragLeave} >
            <form
                className="input-form"
                onSubmit={handleOnSubmit}>
                <input
                    className="input"
                    placeholder="Type your question here..."
                    onChange={handleOnChange}
                    // onFocus={onFocus}
                    value={rawInput}
                />
                <SendButton onClick={handleOnSubmit} />
            </form>
        </div>
    );
}

export default Input;
