import React, { FC } from "react";
import { UserInfo } from "../../xapp/UserInfo";
import { ChatMessageBubble } from "../ChatMessageBubble";
import { ChatMessagePart } from "../ChatMessagePart";
import { TypingIndicator } from "../TypingIndicator";

export interface Props {
    readonly agents: {
        readonly [nick: string]: UserInfo;
    };
}

/**
 * Show the agent is typing (or the bot - for fun)
 */
const TypingStatus: FC<Props> = props => {
    const agent_names = Object.values(props.agents)
        .filter(agent => agent.typing);

    return (
        <>{
            agent_names.map(agent => {
                return (
                    <div className="chat-msg-container-wrapper" key={`typing-status-${agent.nick}`}>
                        <div key={agent.nick} className="chat-msg-container agent chat-typing-progress">
                            <ChatMessagePart user={agent} showAvatar={true}>
                                <div className="chat-msg">
                                    <ChatMessageBubble owner="others" hasTail={true}>
                                        <TypingIndicator />
                                    </ChatMessageBubble>
                                </div>
                            </ChatMessagePart>
                        </div>
                    </div>
                );
            })
        }</>
    )

}

export default TypingStatus;
