import { WidgetEnv } from "@xapp/stentor-chat-widget";
import React, { FC } from "react";
import ReactDOM from "react-dom";
import ChatWidgetContainer from "./components/ChatWidget/ChatWidgetContainer";
import { parseEnv } from "./utils/parseEnv";
import useFetchStylesheet from "./utils/useFetchStylesheet";

declare global {
    interface Window {
        xaConfig?: WidgetEnv;
    }
}

export interface WindowWithConfig extends Window {
    readonly xaConfig?: WidgetEnv;
}

/**
 * An external script could have replaced the config on the window.
 */
function injectConfig() {
    // The external script will look for 
    const injected = "{{INJECT}}";

    // The way we test to see if it was injected is by JSON.parse
    try {
        const config = JSON.parse(injected);
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore Not sure of a better way to add this for TypeScript
        window.xaConfig = config;
    } catch (e) {
        // do nothing!
    }
}

function getConfigFromWindow(): Partial<WidgetEnv> {
    if (hasConfig(window)) {
        return window.xaConfig;
    }
    return {};
}

function getConfig(): WidgetEnv {
    const env = parseEnv<WidgetEnv>(process.env);

    return {
        ...env,
        ...getConfigFromWindow()
    };
}

function hasConfig(wd: Window): wd is WindowWithConfig {
    return !!wd && typeof (wd as WindowWithConfig).xaConfig === "object";
}

export interface AppProps {
    readonly env: WidgetEnv;
}

const App: FC<AppProps> = props => {
    const { env } = props;
    const [loaded] = useFetchStylesheet();

    if (env.disabled) {
        return <></>;
    }

    return loaded ? <ChatWidgetContainer config={env} /> : <></>;
}

window.addEventListener("load", () => {
    let widget = document.getElementById("xapp-widget");

    if (!widget) {
        widget = document.createElement("div");
        widget.id = "xapp-widget";
        document.body.appendChild(widget);
    }
    // See if the config was injected on fetch, otherwise it will do nothing
    injectConfig();

    // Pull the config off of window.xaConfig
    const config = getConfig();

    ReactDOM.render(
        <App env={config} />,
        widget
    );
});

export default App;