import { log } from "../../utils";
import { ChatServerAsyncActionType } from "../../utils/useChatServerDispatch";
import { VisitorInfoMessage } from "../../xapp/ChatServer";

export default function sendVisitorInfo(msg: VisitorInfoMessage): ChatServerAsyncActionType {
    return chatServer => () => {
        chatServer.setVisitorInfo(msg, (err?: Error) => {
            if (err) {
                log(err);
            }
        });
    };
}