import { log } from "../../utils";
import { ChatServerAsyncActionType } from "../../utils/useChatServerDispatch";

export default function executeAction(text: string, token?: string): ChatServerAsyncActionType {
    return chatServer => dispatch => {
        chatServer.sendChatMsg({ text, token }, (err?: Error) => {
            if (err) {
                log("Error sending message", err);
                return;
            }
        });

        dispatch({
            type: "synthetic",
            detail: {
                type: "visitor.send.msg",
                msg: {
                    text: !token ? text : `${text} (from the list)`
                },
                timestamp: +Date()
            }
        });
    }
}