import React, { FC } from "react";
import { ChatServerButton, ChatServerListItem } from "../../xapp/ChatServerMessage";
import { ActionItemImage } from "../ActionItem";
import { ActionItem } from "../ActionItem/ActionItem";
import { ChatActionButtons } from "../ChatActionButtons";
import "./ListItem.scss";

export interface ListItemProps {
    readonly item: ChatServerListItem;
    readonly emptyImageVisible?: boolean;
    readonly layout?: "normal" | "titles";
    onButtonClick(button: ChatServerButton): void;
    onExecute(text: string, token?: string): void;
}

const ListItem: FC<ListItemProps> = props => {
    const { item } = props;

    const layout = props.layout || "normal";

    return (
        <ActionItem className="xappw-vlist-item-container"
            item={item}
            onButtonClick={props.onButtonClick}
            onExecute={props.onExecute}
        >
            <div className={`xappw-vlist-item xappw-vlist-item--${layout}`}>
                <div className="xappw-vlist-item__description">
                    {item.title && <div className="xappw-vlist-item__title">
                        <span>{item.title}</span>
                    </div>}
                    {item.subTitle && <div className="xappw-vlist-item__subtitle">
                        <span>{item.subTitle}</span>
                    </div>}
                    {!!item.buttons?.length && <ChatActionButtons buttons={item.buttons} onClick={props.onButtonClick} />}
                </div>
                <ActionItemImage
                    item={item}
                    className="xappw-vlist-item__img"
                    emptyImageVisible={props.emptyImageVisible}
                />
            </div>
        </ActionItem>
    );
};

export default ListItem;