import { Response, UserProfile } from "stentor-models";
import { setAccountStatus, setConnectionStatus } from "../store/actions";
import { ACCOUNT_STATUS_TYPE, ActionType, CONNECTION_STATUS_TYPE } from "../store/ChatAction";
import { err, log } from "../utils";
import { postMessageToStentor, StentorRequest } from "../utils/http-utils";
import { uuid } from "../utils/uuid";
import { responseToMessage } from "../utils/responseToMessage";
import { ChatServer, OfflineMessage, VisitorInfoMessage } from "./ChatServer";
import { ChatMessageRequest, ChatServerMessage } from "./ChatServerMessage";

const PERMISSION_QUESTION_EXPIRATION_MS = 300000; // 5 minutes

export interface StentorDirectChatConfig {
    readonly url: string;
    readonly key: string;
}

export class StentorDirectChat implements ChatServer {
    private _userId = "";
    private _sessionId = "";

    private dispatch!: (action: ActionType) => void;

    private visitorInfo!: VisitorInfoMessage;

    private isNewSession: boolean;

    private permissionRequestPending: boolean;
    private permissionRequestTime: number;
    private permissionRequestType!: "EMAIL" | "PHONE_NUMBER";

    // TODO: This may depend on the skill - so you will have to get it from Stentor (design skill metadata API)
    private readonly avatarPath!: string;

    public getAvatarPath(): string {
        return this.avatarPath;
    }
    private readonly config: StentorDirectChatConfig;

    constructor(config: StentorDirectChatConfig) {
        this.config = config;
        this.permissionRequestPending = false;
        this.permissionRequestTime = -1;
        this.isNewSession = false;
    }

    public init(dispatch: (action: ActionType) => void): void {
        this.dispatch = dispatch;

        // TODO: Authenticate - or at least tell the server who you are
        log(`got init auth: ${this.config.key}`);

        // We are connected
        this.widget.setConnectionStatus("online");
        this.widget.setAccountStatus("online");
    };

    /*
     *   FROM STENTOR TO WIDGET
     */

    private widget = {
        setConnectionStatus: (status: CONNECTION_STATUS_TYPE) => {
            log(`SERVER: connection_update: ${JSON.stringify(status)}`);

            this.dispatch(setConnectionStatus(status));
        },
        setAccountStatus: (status: ACCOUNT_STATUS_TYPE) => {
            log(`SERVER: account_status: ${JSON.stringify(status)}`);
            this.dispatch(setAccountStatus(status));
        },
        sendNewMessage: (data: ChatMessageRequest) => {
            log(`SERVER: new message: ${JSON.stringify(data)}`);

            this.dispatch({
                type: "chat",
                detail: {
                    type: "chat.msg",
                    displayName: data.userName,
                    nick: "agent:robot",
                    msg: data.msg,
                    timestamp: +new Date()
                }
            });
        },
        userJoined: (data: any) => {
            log(`SERVER: user joined: ${JSON.stringify(data)}`);

            this.visitorInfo.token = data.token;

            this.dispatch({
                type: "chat",
                detail: {
                    type: "chat.memberjoin",
                    displayName: data.username,
                    nick: "agent:robot",
                    timestamp: +new Date(),
                    avatarPath: data.avatarPath
                }
            });

        },
        userLeft: (data: any) => {
            log(`SERVER: user left: ${JSON.stringify(data)}`);

            this.dispatch({
                type: "chat",
                detail: {
                    type: "chat.memberleave",
                    displayName: data.username,
                    nick: "agent:robot",
                    timestamp: +new Date()
                }
            });
        },
        typing: () => {
            this.dispatch({
                type: "chat",
                detail: {
                    type: "chat.typing",
                    nick: "agent:robot",
                    typing: true,
                    timestamp: +new Date()
                }
            });
        },
        stopTyping: () => {
            this.dispatch({
                type: "chat",
                detail: {
                    type: "chat.typing",
                    nick: "agent:robot",
                    typing: false,
                    timestamp: +new Date()
                }
            });
        },
        disconnect: () => {
            log("SERVER: disconnect");
            this.dispatch(setAccountStatus("offline"));
            this.dispatch(setConnectionStatus("offline"));
        },
        reconnect: () => {
            log("SERVER: reconnect");
            this.dispatch(setAccountStatus("online"));
        },
        reconnectError: () => {
            log("SERVER: reconnect error");
            this.dispatch(setAccountStatus("offline"));
        }
    }

    /*
     *   FROM WIDGET TO STENTOR
     */

    public sendOfflineMsg(_: OfflineMessage, cb: (error?: Error) => void): void {
        cb();
    }

    public async sendChatMsg(message: ChatServerMessage, cb: (err?: Error) => void): Promise<void> {
        const serviceRequest: ChatMessageRequest = {
            msg: message,
            timestamp: new Date().getTime(),
            agent: false,
            type: "msg"
        };

        const agentResponse = await this.post(serviceRequest);
        if (!agentResponse.userName) {
            agentResponse.userName = "Bot";
        }

        this.widget.sendNewMessage(agentResponse);

        cb();
    }

    public sendTyping(): void {
    }

    public setVisitorInfo(visitorInfo: VisitorInfoMessage, cb: (error?: Error) => void): void {
        this.visitorInfo = visitorInfo;

        this.startSession();
        this.widget.userJoined({
            username: "Bot",
            avatarPath: this.getAvatarPath()
        });

        cb();
    }

    public sendChatRating(): void {
    }

    public sendFile(_: File, cb: (err?: Error) => void): void {
        cb();
    }

    public markAsRead(): void { // mark the messages read
    }

    public flush(): void {
    }

    public dispose(): void {
    }

    private async post(message: ChatMessageRequest): Promise<ChatMessageRequest> {
        let request: StentorRequest;

        // First translate the request to bot dialect

        const now = new Date().getTime();

        if (this.isNewSession && !message?.msg?.text) {
            request = {
                type: "LAUNCH_REQUEST",
                rawQuery: message?.msg?.text,
                sessionId: this._sessionId,
                userId: this._userId,
                isNewSession: true,
                intentId: "LaunchRequest",
                platform: "stentor-platform",
                channel: "widget"
            };
        } else {
            // Pending permission request?

            if (this.permissionRequestPending) {
                const expired = now - this.permissionRequestTime > PERMISSION_QUESTION_EXPIRATION_MS;

                const text = message.msg.text.toLowerCase();
                const isEmail = looksLikeEmail(text);

                const granted: boolean = !expired && isEmail;

                if (granted && isEmail) {
                    this.visitorInfo.email = text;
                }

                const userProfile: UserProfile = {};
                if (granted) {
                    if (this.permissionRequestType === "EMAIL") {
                        userProfile.email = this.visitorInfo.email;
                        userProfile.name = this.visitorInfo.displayName;
                    }
                }

                request = {
                    type: "PERMISSION_GRANT",
                    rawQuery: message.msg.text,
                    sessionId: this._sessionId,
                    userId: this._userId,
                    isNewSession: false,
                    granted,
                    userProfile,
                    intentId: "PermissionGrant",
                    platform: "stentor-platform",
                    channel: "widget"
                };

                // reset
                this.permissionRequestTime = -1;
                this.permissionRequestPending = false;
                this.permissionRequestType = undefined;
            } else if (message.msg.token) {
                request = {
                    type: "OPTION_SELECT_REQUEST",
                    token: message.msg.token,
                    sessionId: this._sessionId,
                    userId: this._userId,
                    isNewSession: false,
                    intentId: "OptionSelect",
                    platform: "stentor-platform",
                    channel: "widget"
                };
            } else {
                request = {
                    type: "INTENT_REQUEST",
                    rawQuery: message.msg.text,
                    sessionId: this._sessionId,
                    userId: this._userId,
                    isNewSession: false,
                    intentId: "NLU_RESULT_PLACEHOLDER",
                    platform: "stentor-platform",
                    channel: "widget"
                };
            }
        }

        log(`Visitor says: ${JSON.stringify(request, undefined, 2)}`);

        // Bot is "typing"
        this.widget.typing();

        // Call bot
        const botResponse: Response = await postMessageToStentor(request, this.config.url, this.config.key).catch((postError: Error) => {
            err(`POST failed: ${postError.message}`);
            return {
                text: `Bot doesn't respond. Reason: ${postError.message}`
            };
        });

        // Bot stopped "typing"
        this.widget.stopTyping();

        log(`Bot says: ${JSON.stringify(botResponse, undefined, 2)}`);

        // Now translate the response to server dialect

        // new session no more
        this.isNewSession = false;

        let responseMessage: ChatMessageRequest = responseToMessage(botResponse);

        if (!responseMessage) {
            responseMessage = {
                type: "userLeft",
                timestamp: now,
                msg: {},
                endSession: true
            };

            responseMessage.msg = {
                text: "Oops. The bot doesn't respond... Try later!"
            };
        }

        return responseMessage;
    }

    private startSession(): void {
        if (this.visitorInfo.visitorId) {
            this._userId = `stentor-widget-user-${this.visitorInfo.visitorId}`;
        } else if (this.visitorInfo.email) {
            this._userId = `stentor-widget-user-${this.visitorInfo.email}`;
        } else {
            this._userId = `stentor-widget-user-${uuid()}`;
        }

        this._sessionId = `stentor-widget-session-${uuid()}`;
        this.isNewSession = true;
    }

    // private restartSession(): void {
    //     this._sessionId = `stentor-chat-session-${uuid()}`;
    //     this.isNewSession = true;
    // }

    // private endSession(): Promise<void> {
    //     return Promise.resolve(undefined);
    // }

    get userId(): string {
        return this._userId;
    }

    get sessionId(): string {
        return this._sessionId;
    }
}

function looksLikeEmail(email: string) {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
}
