import React, { FC, useCallback } from "react";
import { executeAction, sendVisitorInfo } from "../../store/actions";
import { useChatServerDispatch } from "../../utils/useChatServerDispatch";
import { MessageFormData } from "../MessageForm";
import PrechatForm from "./PrechatForm";

export interface PrechatFormContainerProps {

}

const PrechatFormContainer: FC<PrechatFormContainerProps> = _ => {

    const dispatch = useChatServerDispatch();

    const handleSubmit = useCallback((data: MessageFormData) => {
        // Don't send empty messages
        if (!data.message) return;

        dispatch(sendVisitorInfo({
            displayName: data.name,
            email: data.email
        }));

        dispatch(executeAction(data.message));
    }, [dispatch]);

    return <PrechatForm
        onSubmit={handleSubmit}
    />
};

export default PrechatFormContainer;