import React, { FC } from "react";
import { ChatDetail } from "../../store/ChatAction";

export interface Props {
    readonly message: ChatDetail;
}

function getMessageByType(msg: ChatDetail) {
    switch (msg.type) {
        case "chat.memberjoin":
            return `${msg.displayName} has joined the chat`;
        case "chat.memberleave":
            return `${msg.displayName} has left the chat`;
        case "chat.rating":
            if (!msg.newRating) {
                return "You have removed the chat rating";
            } else {
                const rating = convertToSentenceCase(msg.newRating);
                return `You have rated the chat service ${rating}`
            }
        default:
            return JSON.stringify(msg);
    }
}

const SystemMessage: FC<Props> = props => {
    return (
        <div className="system-msg-container">
            <span className="system-msg">{getMessageByType(props.message)}</span>
        </div>
    );
}

function convertToSentenceCase(s: string) {
    return s[0].toUpperCase() + s.slice(1);
}

export default SystemMessage;
