import React, { FC, useMemo, useState } from "react";
import { useWidgetEnv } from "../../config";
import { set } from "../../utils";
import { ChatMenu } from "../ChatMenu";
import { DrawerBars } from "../DrawerBars";
import { Input } from "../Input";
import "./ChatFooter.scss";

export interface ChatFooterProps {
    readonly isChatting: boolean;
    onSubmit(message: string): void;
    onChange(): void;
    onFileUpload(event: React.DragEvent<HTMLInputElement>): void;
}

const ChatFooter: FC<ChatFooterProps> = props => {

    const [drawer, setDrawerState] = useState<boolean>(false); // false initially

    const menuItemsRaw = useWidgetEnv()?.menu?.items;
    const menuItems = useMemo<readonly string[]>(() => menuItemsRaw ? menuItemsRaw.map(item => item.label) : [], [menuItemsRaw]);

    function toggleDrawer() {
        const newDrawer = !drawer;
        setDrawerState(newDrawer);
        set("drawer", newDrawer);
    }

    function handleMenuItem(label: string) {
        setDrawerState(false);
        props.onSubmit(label);
    }

    return (<div className="chat-footer">
        {menuItems.length ?
            <>
                <ChatMenu opened={drawer} onItemClick={handleMenuItem} items={menuItems} />
                <div className="chat-footer__menu-icon">
                    <DrawerBars onToggle={toggleDrawer} />
                </div>
            </> : <></>}
        <Input
            addClass={"chat-footer__input " + (props.isChatting ? "visible" : "")}
            onSubmit={props.onSubmit}
            onChange={props.onChange}
            // onFocus={this.inputOnFocus}
            onFileUpload={props.onFileUpload}
        />
    </div>);

};


export default ChatFooter;