import React, { FC, useCallback } from "react";
import { UserInfo } from "../../xapp/UserInfo";
import { ActionButton } from "../ActionButton";
import { CardContainer } from "../CardContainer";

export interface Props {
    readonly agent: UserInfo,
    readonly hasRating: boolean;
    readonly shouldDisplay: boolean;
    onRate(rating: "bad" | "good" | undefined): void;
}

const defaultProps: Props = {
    agent: {} as UserInfo,
    hasRating: false,
    shouldDisplay: true,
    onRate: () => {
        // no-op
    }
};

const ChatRating: FC<Props> = (props = defaultProps) => {

    const { onRate } = props;

    const rateBadButtonOnClick = useCallback(() => {
        onRate("bad");
    }, [onRate]);

    const rateGoodButtonOnClick = useCallback(() => {
        onRate("good");
    }, [onRate]);

    const rateAgainButtonOnClick = useCallback(() => {
        onRate(undefined);
    }, [onRate]);

    // Do not think this prop should be here
    if (!props.shouldDisplay) {
        return null;
    }

    if (!props.hasRating) {
        return (
            <CardContainer title="Chat Rating" addClass="chat-rating-card">
                {props.agent.displayName} has requested you to rate the chat service.
                <div className="buttons-container">
                    <ActionButton
                        addClass="button button-rate-bad"
                        label="Rate Bad"
                        onClick={rateBadButtonOnClick}
                    />
                    <ActionButton
                        addClass="button button-rate-good"
                        label="Rate Good"
                        onClick={rateGoodButtonOnClick}
                    />
                </div>
            </CardContainer>
        );
    } else {
        return (
            <CardContainer title="Chat Rating" addClass="chat-rating-card">
                You have rated the chat service.
                <div className="buttons-container">
                    <ActionButton
                        addClass="button button-rate-again"
                        label="Rate again"
                        onClick={rateAgainButtonOnClick}
                    />
                </div>
            </CardContainer>
        )
    }
}

export default ChatRating;

