import { ActionType, ChatAction, ChatDetail } from "../ChatAction";
import { ChatState } from "../ChatState";
import { DEFAULT_STATE } from "../DefaultState";
import { log } from "../../utils";
import { joinMessages } from "./joinMessages";
import { resetReducer } from "./resetReducer";
import connectionReducer from "./connectionReducer";
import { memberJoin } from "./memberJoin";
import { memberLeave } from "./memberLeave";

// todo: create reducer (requires redux-thunk dependncy)
function appendMessageToState(state: ChatState, msg: ChatDetail) {
    state.chats = joinMessages(state.chats, msg);
  }
  
function update(state = DEFAULT_STATE, action: ActionType): ChatState {
    log("action", action);
  
    if (action.type === "reset") {
      return resetReducer(state);
    }
  
    // workaround until state is split. Use combineReducers afterwards
    const newConnectionState = connectionReducer(state.connection, action);
    if (state.connection !== newConnectionState) {
      state = {
        ...state,
        connection: newConnectionState
      }
    }
  
    if (action.detail) {
      state.lastTimestamp = action.detail.timestamp;
    }
  
    switch (action.type) {
  
      case "account_status":
        return {
          ...state,
          accountStatus: action.detail.status
        };
  
      case "department_update":
        return {
          ...state,
          departments: {
            ...state.departments,
            [action.detail.id]: {
              ...action.detail
            }
          }
        };
  
      case "visitor_update":
        return {
          ...state,
          visitor: {
            ...state.visitor,
            ...action.detail
          }
        };
  
      case "agent_update":
        return {
          ...state,
          agents: {
            ...state.agents,
            [action.detail.nick]: {
              ...action.detail,
              typing: (state.agents[action.detail.nick] || { typing: false }).typing // if we had it - keep it
            }
          }
        };
  
      case "chat":
        const newState = { ...state };
  
        switch (action.detail.type) {
  
          case "chat.memberjoin":
            return memberJoin(state, action.detail);
  
          case "chat.memberleave":
            return memberLeave(state, action.detail);
  
          case "chat.queue.position":
            newState.queuePosition = action.detail.queuePosition;
  
            return newState;
  
          case "chat.request.rating":
            appendMessageToState(newState, action.detail);
  
            return {
              ...newState,
              lastRatingRequestTimestamp: action.detail.timestamp
            };
  
          case "chat.rating":
            appendMessageToState(newState, action.detail);
  
            return {
              ...newState,
              hasRating: !!action.detail.newRating
            };
  
          case "chat.file":
          case "chat.msg":
            appendMessageToState(newState, action.detail);
            const detail = action.detail;
            const msg = detail.type === "chat.msg" ? detail.msg : null;
            newState.chips = msg?.options?.length ? msg.options : [];
  
            return newState;
  
          case "chat.typing":
            const agent = state.agents[action.detail.nick];
  
            return {
              ...state,
              agents: {
                ...state.agents,
                [action.detail.nick]: {
                  ...agent,
                  typing: action.detail.typing
                }
              }
            };
          default:
            return state;
        }
      default:
        log("Unhandled redux action: ", action.type);
        return state;
    }
  }
  
  
export function storeHandler(state = DEFAULT_STATE, action: ActionType): ChatState {
    let result
    let new_action: ChatAction;
  
    if (action.type === "synthetic") {
      log("synthetic action", action); // simulated action by the app
  
      const newTimestamp = state.lastTimestamp + 1;
  
      switch (action.detail.type) {
        case "visitor.send.msg":
          new_action = {
            type: "chat",
            detail: {
              type: "chat.msg",
              displayName: state.visitor.displayName,
              nick: "visitor:",
              timestamp: newTimestamp,
              msg: action.detail.msg
            }
          };
  
          break;
        case "visitor.send.file":
          new_action = {
            type: "chat",
            detail: {
              type: "chat.file",
              displayName: state.visitor.displayName,
              nick: "visitor:",
              timestamp: newTimestamp,
              attachment: action.detail.attachment
            }
          }
  
          break;
        default:
          throw new Error(`Unknown synthetic action. Detail type: ${(action.detail as any).type}`);
      }
  
      result = update(state, new_action);
    } else {
      result = update(state, action);
    }
  
    return result;
  }