import { useEffect, useMemo, useRef } from "react";
import { useSelector } from "react-redux";
import { sendGreeting, sendVisitorInfo } from "../store/actions";
import { ChatState } from "../store/ChatState";
import { ChatServerDispatch, useChatServerDispatch } from "./useChatServerDispatch";

interface ServerVisitorSnapshot {
    readonly dispatch: ChatServerDispatch;
    readonly visitorId: string;
}

function useChatServerVisitorId(): ServerVisitorSnapshot {
    const dispatch = useChatServerDispatch();
    const clientVisitorId = useSelector<ChatState, string>(state => state.visitorId);
    return useMemo<ServerVisitorSnapshot>(() => ({
        dispatch,
        visitorId: clientVisitorId
    }), [dispatch, clientVisitorId]);
}

//send whenever server settings or visitor id changes

export default function useGreeting(active: boolean): void {
    const curr = useChatServerVisitorId();
    const snapshotRef = useRef<ServerVisitorSnapshot | null>(null);

    useEffect(() => {
        if (active) {
            if (snapshotRef.current !== curr) {
                snapshotRef.current = curr;

                if (curr.visitorId) {

                    curr.dispatch(sendVisitorInfo({
                        name: "Visitor",
                        visitorId: curr.visitorId
                    }));

                    const timeoutId = setTimeout(() => {
                        const greetingAction = sendGreeting();
                        curr.dispatch(greetingAction);
                    }, 1000);

                    return () => {
                        clearTimeout(timeoutId);
                    };
                }
            }
        }
        return undefined;
    }, [curr, active]);

}