import { useCallback, useContext } from "react";
import { useDispatch } from "react-redux";
import { AsyncActionType, ChatDispatch } from "../store/actions/types";
import { ChatServer } from "../xapp/ChatServer";
import { ChatServerContext } from "../xapp/XappChat";

export type ChatServerAsyncActionType = (chatServer: ChatServer) => AsyncActionType;
export type ChatServerDispatch = (action: ChatServerAsyncActionType) => void;

export function useChatServerDispatch(): ChatServerDispatch {

    const inner = useDispatch<ChatDispatch>();
    const chatServer = useContext(ChatServerContext);

    return useCallback<ChatServerDispatch>((action: ChatServerAsyncActionType) => {
        if (!action) {
            console.warn("react bug? Someone invokes with null");
            return;
        }
        if (chatServer) {
            inner(action(chatServer));
        }
    }, [inner, chatServer]);
}
