import React, { FC, useCallback } from "react";
import { sendChatRating } from "../../store/actions";
import { useChatServerDispatch } from "../../utils/useChatServerDispatch";
import { UserInfo } from "../../xapp/UserInfo";
import ChatRating from "./ChatRating";

export interface ChatRatingContainerProps {
    readonly agent: UserInfo,
    readonly hasRating: boolean;
    readonly shouldDisplay: boolean;
}

const ChatRatingContainer: FC<ChatRatingContainerProps> = (props) => {
    const dispatch = useChatServerDispatch();

    const handleRate = useCallback((rating: "bad" | "good" | undefined) => {
        dispatch(sendChatRating(rating));
    }, [dispatch]);

    return <ChatRating
        agent={props.agent}
        hasRating={props.hasRating}
        shouldDisplay={props.shouldDisplay}
        onRate={handleRate}
    />;

};

export default ChatRatingContainer;