const DEFAULT_VISITOR: VisitorUpdateDetail = {
    timestamp: 0,
    displayName: "Visitor",
    typing: false
}

export function createDefaultState(): ChatState {
    return {
        connection: {
            connectionStatus: "offline",
            token: null,
            greetingRequested: false
        },
        accountStatus: "offline",
        departments: {},
        visitor: DEFAULT_VISITOR,
        agents: {},
        chats: [],
        lastTimestamp: 0,
        lastRatingRequestTimestamp: 0,
        hasRating: false,
        isChatting: false,
        queuePosition: 0,
        visitorId: visitorFingerprint(),
        chips: []
    };
}

export const DEFAULT_STATE: ChatState = createDefaultState();