/**
 * This mirrors the declaration in Chat-Server
 */

/**
 * It's the same request in both (visitor <-> agent) direction!
 */
export interface ChatMessageRequest {
    type: "rating"
    | "ratingRequest"
    | "comment"
    | "msg"
    | "custom"
    | "startTyping"
    | "stopTyping"
    | "userJoined"
    | "userLeft"
    | "handOff"
    | "handBack"
    | "permissionRequest"
    | "permissionGrant";

    timestamp: number;

    msg?: ChatServerMessage;
    userName?: string;

    avatarPath?: string;

    agent?: boolean; // as opposed to visitor

    endSession?: boolean;

    handoffMessage?: string;
    handoffTarget?: string;

    permissionRequestType?: "EMAIL" | "PHONE";
}

export interface ChatServerMessage {
    readonly text?: string;
    readonly html?: string;
    options?: (string | ChatServerActionLink)[]; // attachments
    card?: ChatServerCard;
    list?: ChatServerList;
    token?: string;
}

export interface ChatServerCard {
    title?: string;
    imageUrl?: string;
    imageActionUrl?: string;
    content?: string;
    buttons?: ChatServerButton[]
}

export type ChatServerListType = "LIST" | "CAROUSEL";

export interface ChatServerList {
    readonly type: ChatServerListType;
    readonly title?: string;
    readonly items: readonly ChatServerListItem[];
}

export interface ChatServerButton {
    actionUrl: string | null;
    label: string;
}

export interface ChatServerActionLink {
    actionUrl: string;
    label: string;
}

export function isChatServerActionLink(arg: string | ChatServerActionLink): arg is ChatServerActionLink {
    return arg.hasOwnProperty("label");
}

export function getChatServerActionLinkLabel(arg: string | ChatServerActionLink): string {
    if (isChatServerActionLink(arg)) {
        return arg.label;
    }
    return arg;
}


export function getItemUrl(item: ChatServerListItem): string | null {
    return item.url || item.imageActionUrl;
 }

export interface ChatServerListItem {
    title: string;
    url?: string;
    imageUrl?: string;
    imageActionUrl?: string;
    subTitle?: string;
    token?: string;
    buttons?: readonly ChatServerButton[]
}
