import { ActionType } from "../store/ChatAction";
import { log } from "../utils";
import { ChatServer, OfflineMessage, VisitorInfoMessage } from "./ChatServer";
import { ChatServerMessage } from "./ChatServerMessage";

export class LogChat implements ChatServer {

    private readonly inner: ChatServer;

    constructor(inner: ChatServer) {
        this.inner = inner;
    }

    public init(dispatch: (action: ActionType) => void): void {
        this.inner.init(dispatch);
    }

    public sendOfflineMsg(message: OfflineMessage, cb: (error?: Error) => void): void {
        log(`CLIENT: sendOfflineMsg: ${JSON.stringify(message)}`);
        this.inner.sendOfflineMsg(message, cb);
    }

    public sendChatMsg(message: ChatServerMessage, cb: (err?: Error) => void): void {
        log(`CLIENT: sendChatMsg: ${JSON.stringify(message)}`);
        this.inner.sendChatMsg(message, cb);
    }

    public sendTyping(isTyping: boolean): void {
        log(`CLIENT: sendTyping: ${JSON.stringify(isTyping)}`);
        this.inner.sendTyping(isTyping);
    }

    public setVisitorInfo(visitorInfo: VisitorInfoMessage, cb: (error?: Error) => void): void {
        log(`CLIENT: setVisitorInfo: ${JSON.stringify(visitorInfo)}`);
        this.inner.setVisitorInfo(visitorInfo, cb);
    }

    public sendChatRating(rating?: "bad" | "good"): void {
        log(`CLIENT: sendChatRating: ${JSON.stringify(rating)}`);
        this.inner.sendChatRating(rating);
    }

    public sendFile(file: File, cb: (err?: Error) => void): void {
        log(`CLIENT: sendFile: ${JSON.stringify(file)}`);
        this.inner.sendFile(file, cb);
    }

    public markAsRead(): void {
        log(`CLIENT: markAsRead:`);
        this.inner.markAsRead();
    }

    public flush(): void {
        log(`CLIENT: flush:`);
        this.inner.flush();
    }

    public dispose(): void {
        log(`CLIENT: dispose:`);
        this.inner.dispose();
    }

}