import { isAgent } from "../../utils/nick-utils";
import { ChatMemberLeaveDetail } from "../ChatAction";
import { ChatState } from "../ChatState";
import { joinMessages } from "./joinMessages";

export function memberLeave(state: ChatState, detail: ChatMemberLeaveDetail): ChatState {
    if (isAgent(detail.nick)) {
        const prevAgentInfo = state.agents[detail.nick];
        return {
            ...state,
            isChatting: false,
            chats: prevAgentInfo?.joined ? joinMessages(state.chats, detail) : state.chats,
            agents: {
                [detail.nick]: {
                    ...prevAgentInfo,
                    joined: false,
                }
            }
        };
    } else {
        return {
            ...state,
            isChatting: false,
            chats: joinMessages(state.chats, detail)
        };
    }
}