import { WidgetHeaderConfig, WidgetHeaderStatusConfig } from "@xapp/stentor-chat-widget";
import React, { FC } from "react";
import { UserInfo } from "../../xapp/UserInfo";
import { Avatar } from "../Avatar";
import { MinimizeButton } from "../MinimizeButton";
import "./StatusContainer.scss";

export interface Props {
  accountStatus: string; // TODO: use enum
  readonly canMinimize: boolean;
  readonly agent: UserInfo;
  readonly config?: WidgetHeaderConfig;
  minimizeOnClick: () => void;
}

const DEFAULT_STATUS_CONFIG: WidgetHeaderStatusConfig = {
  online: "Virtual Assistant",
  offline: "Leave us a message",
  away: "We're away!",
  connecting: "Connecting..."
};

function getStatusText(status: string, config?: WidgetHeaderStatusConfig) {
  switch (status) {
    case "online": return config?.online ?? DEFAULT_STATUS_CONFIG.online;
    case "offline": return config?.offline ?? DEFAULT_STATUS_CONFIG.offline;
    case "away": return config?.away ?? DEFAULT_STATUS_CONFIG.away;
    default: return config?.connecting ?? DEFAULT_STATUS_CONFIG.connecting;
  }
}

const StatusContainer: FC<React.PropsWithChildren<Props>> = props => {
  return (
    <div className="status-container">
      <div className="status-container__avatar">
        <Avatar entity={props.agent} />
      </div>

      <div className="status-text">
        <span>{getStatusText(props.accountStatus, props.config?.status)}</span>
      </div>

      {props.canMinimize &&
        <MinimizeButton onClick={props.minimizeOnClick} />
      }
    </div>
  );
}

export default StatusContainer;
