import { isAgent } from "../../utils/nick-utils";
import { ChatState } from "../ChatState";
import { ChatMemberJoinDetail } from "../ChatAction"
import { joinMessages } from "./joinMessages";

export function memberJoin(state: ChatState, detail: ChatMemberJoinDetail): ChatState {
    if (isAgent(detail.nick)) {
        const prevAgentInfo = state.agents[detail.nick];
        return {
            ...state,
            isChatting: true,
            chats: prevAgentInfo?.joined ? state.chats : joinMessages(state.chats, detail),
            agents: {
                [detail.nick]: {
                    ...prevAgentInfo,
                    avatarPath: detail.avatarPath,
                    displayName: detail.displayName || "Agent",
                    joined: true,
                    nick: detail.nick,
                    typing: false
                }
            }
        };
    } else {
        return {
            ...state,
            isChatting: true,
            chats: joinMessages(state.chats, detail)
        };
    }
}
