import React, { FC, useCallback } from "react";
import { executeAction } from "../../store/actions";
import { ChatDetail, ChatMsgDetail } from "../../store/ChatAction";
import { isAgent } from "../../utils";
import { useChatServerDispatch } from "../../utils/useChatServerDispatch";
import { ChatServerButton, ChatServerMessage } from "../../xapp/ChatServerMessage";
import { UserInfo } from "../../xapp/UserInfo";
import { Carousel } from "../Carousel";
import { ChatCard } from "../ChatCard";
import { ChatMarkdownMessage } from "../ChatMarkdownMessage/ChatMarkdownMessage";
import { ChatMessagePart } from "../ChatMessagePart";
import { ChatTextMessage } from "../ChatTextMessage/ChatTextMessage";
import { List } from "../List";
import "./ChatMessage.scss";

export interface ChatMessageProps {
    message: ChatMsgDetail;
    sibling?: boolean;
    agent: UserInfo;
}

function getClassName(msg: ChatDetail) {
    return isAgent(msg.nick) ? "agent" : "visitor"
}

declare type AvaKey = keyof ChatServerMessage;

const avaKeys: readonly AvaKey[] = ["text", "html", "card", "list"]

/**
 * Chat Message Bubble with an Avatar
 * @param props 
 * @returns 
 */
const ChatMessage: FC<React.PropsWithChildren<ChatMessageProps>> = props => {

    const dispatch = useChatServerDispatch();

    const handleButton = useCallback((button: ChatServerButton) => {
        if (button.actionUrl) {
            window.open(button.actionUrl, "chat-button", "toolbar=0,status=0,width=1000,height=700");
        } else {
            dispatch(executeAction(button.label));
        }
    }, [dispatch])

    function handleExecute(text: string, token?: string) {
        dispatch(executeAction(text, token));
    }

    function renderByType() {
        const user = props.agent;
        const msg = props.message.msg;
        switch (props.message.type) {
            // TODO: props actually requires it to be "chat.msg". Fix prop typing?
            case "chat.msg":

                // Here is the deal. If we have text (output speech), then text - card - list - options
                // OR card OR list only. Avatar with text bubble.

                const avaKey = avaKeys.find(key => !!msg[key]);

                return (
                    <>
                        { msg.card &&
                            <ChatMessagePart showAvatar={avaKey === "card"} user={user}>
                                <div className="chat-msg">
                                    <ChatCard card={msg.card} onButtonClick={handleButton} />
                                </div>
                            </ChatMessagePart>
                        }

                        { msg.text &&
                            <ChatMessagePart showAvatar={avaKey === "text"} user={user}>
                                <ChatTextMessage message={props.message} sibling={props.sibling} />
                            </ChatMessagePart>
                        }
                        { msg.html &&
                            <ChatMessagePart showAvatar={avaKey === "html"} user={user}>
                                <ChatMarkdownMessage message={props.message} sibling={props.sibling} />
                            </ChatMessagePart>
                        }

                        { msg.list && <ChatMessagePart showAvatar={avaKey === "list"} user={user}>
                            {msg.list.type === "CAROUSEL" && <div className="chat-msg chat-msg--expand chat-msg--no-ava"><Carousel list={msg.list} onExecute={handleExecute} onButtonClick={handleButton} /></div>}
                            {msg.list.type === "LIST" && <div className="chat-msg chat-msg--expand"><List list={msg.list} onExecute={handleExecute} onButtonClick={handleButton} /></div>}
                        </ChatMessagePart>}
                    </>
                );

            // TODO: do file response?
            // case "chat.file":
            //     return (
            //         <div className={`chat-msg-container ${getClassName(props.message)} ${props.addClass}`}>
            //             {renderAvatar(props.agent)}
            //             <div className="chat-msg-wrapper">
            //                 <ChatMedia message={messageDetail} />
            //                 {renderOptions(props.message.msg.options)}
            //             </div>
            //         </div>
            //     );
            default:
                break;
        }

        return (<></>);

    }

    // empty
    return (
        <div className={`chat-msg-container-wrapper ${isAgent(props.message.nick) ? "agent" : "visitor"} ${props.sibling ? "sibling" : ""}`}>
            <div className={`chat-msg-container ${getClassName(props.message)}`}>
                {renderByType()}
            </div>
        </div>
    );
}

export default ChatMessage;
