import { CtaConfig, ServerConfig, WidgetEnv } from "@xapp/stentor-chat-widget";
import { createContext, useContext } from "react";

const paramsObj: { [key: string]: string } = {};

export function getBasePathFromWidgetSource(): { path: string, key?: string } {
  // the ID "xapp-js" is on the injected <script id="xapp-js" src="..." ></script>
  const xappScriptNode = document.getElementById("xapp-js") as HTMLScriptElement | undefined;
  const path = getBasePath(xappScriptNode?.src);
  return path;
}


// todo: read only from xappScriptNode?
for (const script of (document as any).scripts) {
  if (!script.src) {
    continue
  }

  const params = script.src.split("?");
  if (params.length < 2) {
    continue;
  }

  const searchParams = new URLSearchParams(params[1]);
  searchParams.forEach(
    (value, key) => {
      paramsObj[key] = value;
    }
  );
}

export const defaultServerUrl = process.env.SERVER_URL;
export const defaultAccountKey = process.env.ACCOUNT_KEY;

// TODO: Review these
export interface Config {
  readonly connection: ServerConfig;
  readonly assetUrl?: string;
  readonly query?: QueryMap;
  readonly cta?: CtaConfig;
  readonly env?: WidgetEnv;
}

interface QueryMap {
  readonly [key: string]: string;
}

export function getBasePath(path?: string): { path: string, key?: string } {
  if (!path) {
    return { path };
  }
  const lastPathSep = path.lastIndexOf("/");
  let result = "";
  if (lastPathSep >= 0) {
    result = path.substr(0, lastPathSep);
  }

  const url = new URL(path);
  const params = new URLSearchParams(url.search);
  const key = params.get("key");

  return { path: result, key };
}

export const ChatConfigContext = createContext<Config>(null);

export function useWidgetEnv(): WidgetEnv | undefined {
  const ctx = useContext(ChatConfigContext);
  return ctx?.env;
}
