import { logOnce } from "../../utils";

export type Reducer<TState, TAction> = (state: TState, action: TAction) => TState;

function tryParseJson<T>(str: string | null): T | undefined {
    try {
        if (str) {
            return JSON.parse(str);
        }
    } catch {
    }
    return undefined;
}

export default function persistStateReducer<TState, TAction>(initialState: TState, innerReducer: Reducer<TState, TAction>): Reducer<TState, TAction> {
    const key = "xappchat";
    const storage = sessionStorage;
    const obj = tryParseJson<TState>(storage.getItem(key)) ?? initialState;
    return (state = obj, action) => {
        const res = innerReducer(state, action);
        try {
            storage.setItem(key, JSON.stringify(res));
        } catch {
            logOnce("storage", "Unable to store redux state");
        }
        return res;
    };
}