import { default as React, FC } from "react";
import { useSelector } from "react-redux";
import { executeAction } from "../../store/actions";
import { ChatState } from "../../store/ChatState";
import { useChatServerDispatch } from "../../utils/useChatServerDispatch";
import { ChatServerActionLink, getChatServerActionLinkLabel, isChatServerActionLink } from "../../xapp/ChatServerMessage";
import ChatChips from "./ChatChips";

export interface Props {
}

const ChatChipsContainer: FC<Props> = _ => {
    const dispatch = useChatServerDispatch();
    const chips = useSelector<ChatState, (string | ChatServerActionLink)[]>(state => state.chips);

    function optionOnChange(option: string | ChatServerActionLink) {
        const actionUrl = isChatServerActionLink(option) && option.actionUrl;

        if (actionUrl) {
            window.open(actionUrl, "callout-option", "toolbar=0,status=0,width=1000,height=700");
        } else {
            dispatch(executeAction(getChatServerActionLinkLabel(option)));
        }
    }
    return (
        <div className={`message-list-container__chips ${!chips?.length ? "message-list-container__chips--empty" : ""}`}>
            <ChatChips options={chips} onOptionClick={optionOnChange} />
        </div>

    );
}

export default ChatChipsContainer;
