import React, { FC } from "react";

export interface Props {
    onClick(): void;
}

function getRightArrowSvg() {
    return (
        <svg viewBox="-5 -18 10 36">
            <path d="M -2.5 -15 L 2.5 0 L -2.5 15" stroke="currentColor" strokeLinecap="square" strokeWidth="4px" fill="none" />
        </svg>
    );
}

const ChevronRight: FC<Props> = props => {
    return (
        <div onClick={props.onClick} className="chevron" >
            {getRightArrowSvg()}
        </div>
    );
};

export default ChevronRight;
