import React, { FC } from "react";
import { UserInfo } from "../../xapp/UserInfo";
import { Avatar } from "../Avatar";
import "./ChatMessagePart.scss";

export interface ChatMessagePartProps {
    readonly showAvatar: boolean;
    readonly user: UserInfo;
}

function renderAvatar(entity: UserInfo) {
    return (
        <div className="xappw-chat-msg-part__avatar">
            <Avatar entity={entity} />
        </div>
    );
}


export const ChatMessagePart: FC<ChatMessagePartProps> = props => {
    return (
        <div className="xappw-chat-msg-part">
            { props.showAvatar && renderAvatar(props.user)}
            { props.children}
        </div>
    );
};

