import { applyMiddleware, compose, createStore } from "redux";
import thunk from "redux-thunk";
import { DEFAULT_STATE } from "./DefaultState";
import persistStateReducer from "./reducers/persistStateReducer";
import { storeHandler } from "./reducers/rootReducer";

export const chatReducer = persistStateReducer(DEFAULT_STATE, storeHandler);

const middlewares = [
  thunk
];

const composeEnhancers = (window as any).__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

const ChatStore = createStore(chatReducer, composeEnhancers(applyMiddleware(...middlewares)));

export default ChatStore;
