import React from "react";
import { FC } from "react";

export interface OptionalLinkProps {
    readonly url?: string;
    readonly className?: string;
    onClick: () => void;
}

export const OptionalLink: FC<OptionalLinkProps> = props => {
    const { url, className } = props;

    function openUrl() {
        window.open(url, "callout-option", "toolbar=0,status=0,width=1000,height=700");
    }
    if (url) {
        return (
            <div onClick={openUrl} className={className}>
                {props.children}
            </div>
        );
    } else {
        return (
            <div onClick={props.onClick} className={className}>
                {props.children}
            </div>
        );
    }
};
