import { ServerConfig, WidgetEnv } from "@xapp/stentor-chat-widget";
import React, { FC, useContext, useEffect, useMemo, useState } from "react";
import { useSelector } from "react-redux";
import { ChatConfigContext, Config, defaultServerUrl } from "../../config";
import { executeAction, sendFile, sendTyping } from "../../store/actions";
import { CONNECTION_STATUS_TYPE } from "../../store/ChatAction";
import { ChatState } from "../../store/ChatState";
import { get, set } from "../../utils";
import { useChatServerDispatch } from "../../utils/useChatServerDispatch";
import useGreeting from "../../utils/useGreeting";
import { ChatServerContext, useChatServer } from "../../xapp/XappChat";
import { ChatButton } from "../ChatButton";
import { ChatFooter } from "../ChatFooter";
import { MessageList } from "../MessageList";
import { ServerOffline } from "../ServerOffline";
import { StatusContainer } from "../StatusContainer";
import { WidgetStylesheet } from "../WidgetStylesheet";
import "./ChatWidget.scss";

export interface ChatWidgetProps {
  readonly preChatFormEnabled?: boolean;
  readonly config?: WidgetEnv;
  readonly mode?: ChatWidgetMode;
  onConnectionStatusChange?(status: CONNECTION_STATUS_TYPE): void;
}

export interface ChatWidgetWrapperProps extends ChatWidgetProps {
}

export type ChatWidgetMode = "normal" | "docked" | "static";

function useConnectionInfo(env?: WidgetEnv): ServerConfig {
  const connection = useMemo<ServerConfig>(() => {
    if (env?.connection) {
      return env.connection;
    }
    return {
      serverUrl: env?.serverUrl,
      accountKey: env?.accountKey,
      type: env?.direct ? "direct" : "websocket"
    };
  }, [env]);

  const nonce = useSelector<ChatState, string | undefined>(state => state.connection.nonce);

  return useMemo<ServerConfig>(() => ({
    accountKey: connection.accountKey,
    serverUrl: connection.serverUrl,
    type: connection.type,
    nonce
  }), [connection.accountKey, connection.serverUrl, connection.type, nonce]);
}

const ChatWidgetWrapper: FC<ChatWidgetWrapperProps> = props => {
  const rawConfig = props.config;
  const connection = useConnectionInfo(rawConfig);

  const config = useMemo<Config>(() => ({
    ...rawConfig,
    connection,
    assetUrl: connection?.serverUrl ?? defaultServerUrl,
    env: rawConfig
  }), [connection, rawConfig]);

  const chatServer = useChatServer(connection);

  return (
    <ChatConfigContext.Provider value={config}>
      <ChatServerContext.Provider value={chatServer}>
        <ChatWidget
          {...props}
        />
      </ChatServerContext.Provider>
    </ChatConfigContext.Provider>
  );

};

const ChatWidget: FC<ChatWidgetProps> = props => {
  const dispatch = useChatServerDispatch();

  // From Redux
  const chatState = useSelector<ChatState, ChatState>(state => state);

  const mode = props.mode ?? "normal";
  const dockedMode = mode === "docked";
  const staticMode = mode === "static";
  const modeClass = `widget-container--${mode}`;

  const canMinimize = !dockedMode && !staticMode;

  // Our state - pull from storage
  const [visible, setVisibleState] = useState<boolean>(!canMinimize || !!get("visible)") || (props.config?.autoOpenOnWidth && window.matchMedia(`(min-width: ${props.config?.autoOpenOnWidth})`).matches));
  const [typing, setTypingState] = useState<boolean>(false); // false initially

  const chatServer = useContext(ChatServerContext);

  function setVisible(newVisible: boolean) {
    if (staticMode) {
      return;
    }
    setVisibleState(newVisible);
    set("visible", newVisible);
  }

  function handleOnChange() {
    if (!typing) {
      dispatch(sendTyping(true));
      setTypingState(true);
    }

    stopTyping();
  }

  function stopTyping() {
    if (!typing) return;
    dispatch(sendTyping(false));
    setTypingState(false);
  }

  function handleOnSubmit(rawInput: string) {
    // Don't allow visitor to send msg if not chatting
    if (chatState.accountStatus === "offline" && !chatState.isChatting) return;

    // Don't send empty messages
    if (!rawInput) return;

    // Immediately stop typing
    stopTyping();

    chatServer.flush();

    dispatch(executeAction(rawInput));
  }

  function handleFileUpload(event: React.DragEvent<HTMLInputElement>) {
    event.preventDefault();

    // Only send the first file dropped on input
    const file = event?.dataTransfer?.files[0];

    if (!file) {
      return;
    }

    dispatch(sendFile(file))
  }

  function getVisibilityClass() {
    return visible ? "visible" : "";
  }

  function minimizeOnClick() {
    setVisible(false);
  }

  function chatButtonOnClick() {
    setVisible(true);
  }

  const isOffline = chatState.accountStatus === "offline" && !chatState.isChatting;
  const messages = chatState && chatState.chats;
  if (isOffline) {
    messages.push({
      type: "chat.msg",
      displayName: "Chat Agent",
      nick: "agent:offline",
      timestamp: +new Date(),
      msg: { text: "Sorry, we are offline at the moment. Please leave us your contact information and we will get back to you soon!" }
    });

    messages.push({
      type: "chat.offline",
      nick: "agent:offline",
      timestamp: +new Date()
    });
  } else if (!chatState.isChatting) {
    messages.length = 0; // clear the list

    if (props.preChatFormEnabled) {
      messages.push({
        type: "chat.prechat",
        nick: "agent:offline",
        timestamp: +new Date()
      });
    }
  }

  useGreeting(!isOffline && !props.preChatFormEnabled && visible);

  const { config, onConnectionStatusChange } = props;

  const connectionStatus = chatState.connection.connectionStatus;
  useEffect(() => {
    if (onConnectionStatusChange) {
      onConnectionStatusChange(connectionStatus);
    }
  }, [connectionStatus, onConnectionStatusChange]);

  return (
    <>
      <div className={`widget-container ${modeClass} ${getVisibilityClass()}`}>
        <WidgetStylesheet theme={config?.theme} />
        <StatusContainer
          accountStatus={chatState.accountStatus}
          minimizeOnClick={minimizeOnClick}
          agent={chatState.agents["agent:robot"]}
          canMinimize={canMinimize}
          config={config?.header}
        />
        <MessageList
          visible={visible}
          queuePosition={chatState.queuePosition}
          isChatting={chatState.isChatting}
          isOffline={isOffline}
          messages={messages}
          agents={chatState.agents}
          lastRatingRequestTimestamp={chatState.lastRatingRequestTimestamp}
          hasRating={chatState.hasRating}
          visitorId={chatState.visitorId}
        />
        <div className={`spinner-container ${visible && connectionStatus === "pending" ? "visible" : ""}`}>
          <div className="spinner" />
        </div>
        {connectionStatus === "offline" && <ServerOffline />}
        <ChatFooter
          isChatting={chatState.isChatting}
          onChange={handleOnChange}
          onSubmit={handleOnSubmit}
          onFileUpload={handleFileUpload}
        />
      </div>
      <ChatButton addClass={getVisibilityClass()} onClick={chatButtonOnClick} config={props.config?.cta} visible={visible} />
    </>
  );
}

export default ChatWidgetWrapper;
