import { uuid } from "../../utils/uuid";
import { ChatState } from "../ChatState";
import { createDefaultState, DEFAULT_STATE } from "../DefaultState";

export function resetReducer(state: ChatState = DEFAULT_STATE): ChatState {
    return {
        ...createDefaultState(),
        connection: {
            ...state.connection,
            nonce: uuid()
        },
        visitor: state.visitor,
        visitorId: state.visitorId
    };
}
