import { Card, LinkOutSuggestion, List, Response, ResponseOutput, } from "stentor-models";
import { isCard, isList } from "../utils/guards";
import { ChatMessageRequest, ChatServerListItem } from "../xapp/ChatServerMessage";

/**
 * Converts a Stentor Response to a ChatMessageRequest
 * 
 * @param botResponse 
 * @param now 
 * @returns 
 */
export function responseToMessage(botResponse: Response, now: number = new Date().getTime()): ChatMessageRequest {

    let responseMessage: ChatMessageRequest;

    if (!botResponse) {
        return responseMessage;
    }

    let text: string;
    let html: string;
    let card: Card;
    let list: List;
    let endSession: boolean;

    let outputSpeech: ResponseOutput;
    let reprompt: ResponseOutput

    if (botResponse.outputSpeech) {
        if (typeof botResponse.outputSpeech === "string") {
            outputSpeech = {
                displayText: botResponse.outputSpeech
            }
        } else {
            outputSpeech = botResponse.outputSpeech;
        }
    }

    if (botResponse.reprompt) {
        if (typeof botResponse.reprompt === "string") {
            reprompt = {
                displayText: botResponse.reprompt
            }
        } else {
            reprompt = botResponse.reprompt;
        }
    }

    if (outputSpeech) {
        text = outputSpeech.displayText;
        html = outputSpeech.html;
    }

    if (botResponse.displays && botResponse.displays.length > 0) {
        const display = botResponse.displays[0];
        if (isCard(display)) {
            card = display;
        } else if (isList(display)) {
            list = display;
        }
    }

    if (botResponse.system === "TRANSFER_CALL") {
        responseMessage = {
            type: "handOff",
            timestamp: now,
            handoffMessage: text,
            handoffTarget: botResponse?.data?.transferPhoneNumber as string,
            endSession: false
        };
    } else if (botResponse.system?.startsWith("PERMISSION_")) {
        if (botResponse.system === "PERMISSION_EMAIL") {
            responseMessage = {
                type: "permissionRequest",
                permissionRequestType: "EMAIL",
                timestamp: now,
                msg: {},
                endSession: false
            };

            responseMessage.msg = {
                ...responseMessage.msg,
                text: `${botResponse.data.permissionRequestTTSContext}`
            }

            this.permissionRequestPending = true;
            this.permissionRequestTime = new Date().getTime();
            this.permissionRequestType = "EMAIL";
        } else {
            throw new Error(`Unsupported permission: ${botResponse.system}`);
        }
    } else {
        endSession = !reprompt || !(reprompt.displayText || reprompt.ssml);

        responseMessage = {
            type: "msg",
            timestamp: now,
            msg: {},
            endSession
        };

        if (card) {
            responseMessage.msg.card = {
                content: card.content,
                imageUrl: card.smallImageUrl,
                title: card.title,
                imageActionUrl: (card as any)?.imageActionUrl
            };

            if (card.buttons && card.buttons.length > 0) {
                responseMessage.msg.card.buttons = card.buttons.map(button => ({
                    actionUrl: button.openUrlAction,
                    label: button.title
                }));
            }
        }

        if (list) {
            responseMessage.msg.list = {
                type: list.type,
                title: list.title,
                items: list.items.map(item => {
                    const responseItem: ChatServerListItem = {
                        title: item.title,
                        subTitle: item.description,
                        token: item.token,
                        url: item.url,
                        imageUrl: item.image?.url,
                        imageActionUrl:item.image?.imageActionUrl
                    };

                    // TODO: Remove any when the buttons are defined on the list (ListButton - title + openUrlAction)
                    const itemButtons: readonly any[] = (item as any).buttons;

                    if (itemButtons && itemButtons.length > 0) {
                        responseItem.buttons = itemButtons.map(button => ({
                            actionUrl: button.openUrlAction,
                            label: button.title
                        }));
                    }

                    return responseItem;
                })
            };
        }

        if (text && !html) {
            responseMessage.msg = {
                ...responseMessage.msg,
                text
            }
        }

        if (html) {
            responseMessage.msg = {
                ...responseMessage.msg,
                html
            }
        }

        if (text || html) {
            responseMessage.msg.options = [];

            if (outputSpeech.suggestions && outputSpeech.suggestions.length > 0) {
                for (const suggestion of outputSpeech.suggestions) {
                    if (typeof suggestion === "string") {
                        // Simple chips (strings)
                        responseMessage.msg.options.push(
                            suggestion
                        );
                    } else {
                        // "call out" chips
                        responseMessage.msg.options.push(
                            {
                                label: suggestion.title,
                                actionUrl: (suggestion as LinkOutSuggestion).url
                            }
                        );
                    }
                }
            }
        }
    }

    return responseMessage;

}