import React, { FC, useEffect, useMemo, useState } from "react";
import CtaBubble from "./CtaBubble";

export interface Props {
    readonly visible: boolean;
    readonly timeout: number;
    onClick: () => void;
}

const CtaBubbleContainer: FC<React.PropsWithChildren<Props>> = props => {

    const startTime = useMemo<number | undefined>(() => {
        if (props.visible) {
            return new Date().valueOf();
        }
        return undefined;
    }, [props.visible]);

    const [currentTime, setCurrentTime] = useState<number>(new Date().valueOf());

    const timeout = props.timeout;
    const duration = currentTime - startTime;

    const pastDue = startTime && props.timeout && (duration >= props.timeout);

    useEffect(() => {
        if (!startTime) {
            return undefined;
        }
        const end = startTime + timeout;
        const now = new Date().valueOf();
        const left = end - now;
        if (left <= 0) {
            return undefined;
        }
        const handle = setTimeout(() => {
            setCurrentTime(new Date().valueOf());
        }, left);

        return () => {
            clearTimeout(handle);
        };
    }, [startTime, timeout]);

    return (
        <>
            {props.visible && !pastDue && <CtaBubble onClick={props.onClick}>{props.children}</CtaBubble>}
        </>
    );
}

export default CtaBubbleContainer;