import React, { FC, useEffect, useRef } from "react";
import { ChatDetail, ChatMsgDetail } from "../../store/ChatAction";
import { ChatChipsContainer } from "../ChatChips";
import { ChatMessage } from "../ChatMessage";
import { ChatRatingContainer } from "../ChatRating";
import { OfflineFormContainer } from "../OfflineForm";
import PrechatFormContainer from "../PrechatForm/PrechatFormContainer";
import { QueuePosition } from "../QueuePosition";
import { SystemMessage } from "../SystemMessage";
import { TypingStatus } from "../TypingStatus";
import "./MessageList.scss";

export interface Props {
  visible: boolean;
  messages: readonly ChatDetail[];
  agents: any;
  isOffline: boolean;
  isChatting: boolean;
  queuePosition: number;
  lastRatingRequestTimestamp: number;
  hasRating: boolean;
  visitorId: string;
}

const defaultProps: Props = {
  visible: true,
  messages: [],
  agents: {},
  isOffline: true,
  isChatting: false,
  queuePosition: 0,
  lastRatingRequestTimestamp: 0,
  hasRating: false,
  visitorId: ""
};

const MessageList: FC<React.PropsWithChildren<Props>> = (props = defaultProps) => {
  const messagesEndRef = useRef(null);

  useEffect(() => {
    messagesEndRef.current.scrollIntoView({ behavior: "smooth" });
  }, [props.messages]);

  /**
   * Render the message based on widget state or message type. offline and pre chat are pseudo messages.
   *
   * @param msg
   * @param addClass
   */
  function renderByType(msg: ChatDetail, sibling: boolean) {
    switch (msg.type) {
      case "chat.file":
      case "chat.msg":
        return (
          <ChatMessage
            key={msg.type + msg.timestamp}
            message={msg as ChatMsgDetail}
            sibling={sibling}
            agent={props.agents[msg.nick]}
          />
        );
      case "chat.memberjoin":
      case "chat.memberleave":
      case "chat.rating":
      case "chat.typing":
        return (
          <SystemMessage
            key={msg.type + msg.timestamp}
            message={msg}
          />
        );
      case "chat.request.rating":
        return (
          <ChatRatingContainer
            key={msg.type + msg.timestamp}
            agent={props.agents[msg.nick]}
            hasRating={props.hasRating}
            shouldDisplay={msg.timestamp === props.lastRatingRequestTimestamp}
          />
        );
      case "chat.offline":
        return <OfflineFormContainer key={`offline-${msg.timestamp}`} />;
      case "chat.prechat":
        return <PrechatFormContainer key="prechat" />;
      default:
        return <div key={+new Date()}>Unhandled message: {JSON.stringify(msg)}</div>
    }
  }

  /**
   * Render all messages
   *
   */
  function renderAll() {
    return props.messages.map((message: ChatDetail, index: number) => {
      const next = props.messages[index + 1];

      let sibling = false;

      const currentNick = message.nick;
      const prevNick = next && next.nick;

      // Same agent says more
      if (next && next.type === message.type && currentNick && currentNick === prevNick) {
        sibling = true;
      }

      return renderByType(message, sibling);
    });
  }

  const { agents, queuePosition } = props;

  return (
    <div className="message-list-container">
      <div className="message-list-container__msgs">
        {renderAll()}
        <div ref={messagesEndRef} style={{ float: "left", clear: "both" }} />
        <QueuePosition position={queuePosition} />
        <TypingStatus agents={agents} />
      </div>
      <ChatChipsContainer />
    </div>
  );
}

export default MessageList;
