import React, { FC, useCallback } from "react";
import { sendOfflineMsg } from "../../store/actions";
import { useChatServerDispatch } from "../../utils/useChatServerDispatch";
import { MessageFormData } from "../MessageForm";
import OfflineForm from "./OfflineForm";

export interface OfflineFormContainerProps {
}

const OfflineFormContainer: FC<OfflineFormContainerProps> = _ => {

    const dispatch = useChatServerDispatch();

    const handleSubmit = useCallback((data: MessageFormData) => {
        dispatch(sendOfflineMsg(data));
    }, [dispatch]);

    return <OfflineForm
        onSubmit={handleSubmit}
    />
};

export default OfflineFormContainer;