import io from "socket.io-client";
import { setAccountStatus, setConnectionStatus } from "../store/actions";
import receiveToken from "../store/actions/receiveToken";
import { ActionType } from "../store/ChatAction";
import { log } from "../utils";
import { uuid } from "../utils/uuid";
import { ChatServer, OfflineMessage } from "./ChatServer";
import { ChatServerMessage } from "./ChatServerMessage";
import { UserInfo } from "./UserInfo";

export interface StentorServerChatConfig {
    readonly url: string;
    readonly token: string;
}

export class StentorServerChat implements ChatServer {
    private socket!: SocketIOClient.Socket;
    private dispatch!: (action: ActionType) => void;

    private visitorInfo!: UserInfo;
    private token: string;

    private readonly config: StentorServerChatConfig;

    constructor(config: StentorServerChatConfig) {
        this.config = config;
        this.token = config.token;
    }

    public init(dispatch: (action: ActionType) => void): void {
        this.socket = io(this.config.url, {
            transports: ["websocket"],
            reconnection: true,
            reconnectionDelay: 1000,
            reconnectionDelayMax: 5000,
            reconnectionAttempts: 5
        });

        this.dispatch = dispatch;

        // TODO: Authenticate - or at least tell the server who you are

        // Socket events

        this.socket.on("account_status", (data: any) => {
            log(`SERVER: account_status: ${JSON.stringify(data)}`);

            // Register the "communication" with the server
            if (!this.token) {
                this.token = uuid();
                dispatch(receiveToken(this.token));
                log(`CLIENT: created token: ${this.token}`);
            }

            this.socket.emit("connection update", this.token);

            dispatch({
                type: "account_status",
                detail: {
                    status: "online",
                    timestamp: +new Date()
                }
            });
        });

        this.socket.on("new message", (data: any): void => {
            log(`SERVER: new message: ${JSON.stringify(data)}`);

            this.dispatch({
                type: "chat",
                detail: {
                    type: "chat.msg",
                    displayName: data.username,
                    nick: "agent:robot",
                    msg: data.message,
                    timestamp: +new Date()
                }
            });
        });

        this.socket.on("user joined", (data: any): void => {
            log(`SERVER: user joined: ${JSON.stringify(data)}`);

            this.visitorInfo.token = data.token;

            this.dispatch({
                type: "chat",
                detail: {
                    type: "chat.memberjoin",
                    displayName: data.username,
                    nick: "agent:robot",
                    timestamp: +new Date(),
                    avatarPath: data.avatarPath
                }
            });
        });

        this.socket.on("user left", (data: any) => {
            log(`SERVER: user left: ${JSON.stringify(data)}`);

            this.dispatch({
                type: "chat",
                detail: {
                    type: "chat.memberleave",
                    displayName: data.username,
                    nick: "agent:robot",
                    timestamp: +new Date()
                }
            });
        });

        this.socket.on("typing", (data: any) => {
            log(`SERVER: typing: ${JSON.stringify(data)}`);

            this.dispatch({
                type: "chat",
                detail: {
                    type: "chat.typing",
                    nick: "agent:robot",
                    typing: true,
                    timestamp: +new Date()
                }
            });
        });

        this.socket.on("stop typing", (data: any) => {
            log(`SERVER: stop typing: ${JSON.stringify(data)}`);

            this.dispatch({
                type: "chat",
                detail: {
                    type: "chat.typing",
                    nick: "agent:robot",
                    typing: false,
                    timestamp: +new Date()
                }
            });
        });

        this.socket.on("disconnect", () => {
            log("SERVER: disconnect");
            this.dispatch(setAccountStatus("offline"));
            this.dispatch(setConnectionStatus("offline"));
        });

        this.socket.on("reconnect", () => {
            log("SERVER: reconnect");
            this.dispatch(setAccountStatus("online"));
            this.dispatch(setConnectionStatus("online"));
        });

        this.socket.on("reconnect_failed", (data: Record<string, unknown>) => {
            log("SERVER: reconnect_failed", data);
            this.dispatch(setAccountStatus("offline"));
            this.dispatch(setConnectionStatus("offline"));
        });

        this.socket.on("reconnect_error", (data: Record<string, unknown>) => {
            log("SERVER: reconnect_error", data);
            this.dispatch(setAccountStatus("offline"));
        });

        this.dispatch(setConnectionStatus("pending"));

    };

    public sendOfflineMsg(_: OfflineMessage, cb: (error?: Error) => void): void {
        cb();
    }

    public sendChatMsg(message: ChatServerMessage, cb: (err?: Error) => void): void {
        this.socket.emit("new message", message);
        cb();
    }

    public sendTyping(isTyping: boolean): void {
        this.socket.emit(isTyping ? "typing" : "stop typing");
    }

    public setVisitorInfo(visitorInfo: UserInfo, cb: (error?: Error) => void): void {
        this.visitorInfo = visitorInfo;
        this.socket.emit("add user", visitorInfo);
        cb();
    }

    public sendChatRating(): void {
    }

    public sendFile(_: File, cb: (err?: Error) => void): void {
        cb();
    }

    public markAsRead(): void { // mark the messages read
    }

    public flush(): void {
    }

    public dispose(): void {
        this.socket?.disconnect();
    }

}
